$cell-bg: #eee;
$cell-side: 100px;
$base-padding: 10px;
$cell-gap: 2px;
$start-days:
  "mon" 0,
  "tue" ($cell-side + $cell-gap),
  "wed" 2*($cell-side + $cell-gap),
  "thu" 3*($cell-side + $cell-gap),
  "fri" 4*($cell-side + $cell-gap),
  "sat" 5*($cell-side + $cell-gap),
  "sun" 6*($cell-side + $cell-gap);
